.boss-tracker {
    display: flex;
    justify-content: space-around;
    background-color: var( --items-bg-color);
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 5px;
  }

  .boss {
    text-align: center;
  }
  
  .boss-icon {
    position: relative;
    width: 125px; /* Adjust based on image size */
    height: 85px;
    max-height: 80px;
  }
  
  .boss-icon img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  
  .killed-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0.4;
  }

  .boss-status{
    font-size: 12px;
    color: rgb(173, 172, 172);
  }.boss-name {
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
  }
  
  .trophy-icon.gold {
    color: gold;
    margin-right: 5px;
  }
  
  .trophy-icon.silver {
    color: silver;
    margin-right: 5px;
  }
  
  .trophy-icon.bronze {
    color: #cd7f32; /* Bronze color */
    margin-right: 5px;
  }
  
  .boss-status .alive-text {
    color: green;
    font-weight: bold;
  }
  
  .boss-status .alive-text {
    color: green;
    font-weight: bold;
  }

  @media only screen and (max-width:1500px) {
    .boss-tracker {
      display: none;
    }
  }.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .modal-content {
    background-color: var(--bg-color);
    padding: 10px;
    border-radius: 8px;
    text-align: center;
  }
  
  .modal-content iframe {
    width: 100%;
    max-width: 1000px;
    height: 300px;
    margin-bottom: 15px;
  }

  